import React from 'react';
import styles from './ImagePicker.module.css';

export function SelectedImage({ image, onClick }) {
  return (
    <div 
      className={styles.imagePreview} 
      onClick={onClick} 
      role="button" 
      aria-label="Remove image"
    >
      <img src={image} alt="Selected" />
    </div>
  );
} 